import React from "react";

function Footer() {
  function currentYear() {
    var getYear = new Date().getFullYear();
    return getYear;
  }
  const year = currentYear();
  return (
    <footer>
      <p>
        Coded in <strong className="strong">Visual Studio Code</strong>. Built
        with <strong className="strong">React.js</strong> and
        <strong className="strong"> Material UI</strong>, deployed with{" "}
        <strong className="strong">GitHub pages</strong>.
      </p>

      <p id="copyRight">
        Copyright &#169; {year}{" "}
        <a href="#about">
          <strong className="strong">Ashik Ibrahim S</strong>
        </a>
        . All Rights Reserved.
      </p>
    </footer>
  );
}
export default Footer;
